































import Vue from "vue";

export default Vue.extend({
  name: "Header",
  props: {
    routeName: String,
    viewport: Object,
  },
  methods: {
    openMenu() {
      document.body.classList.add("-open-nav");
    },
    closeMenu() {
      document.body.classList.remove("-open-nav");
    },
  },
});
